<template>
  <div id="gnav">
    <ul id="nav">
        <li v-for="(item,idx) in links" v-bind:key="idx"> <a v-bind:href="item.ul">{{item.title}}</a>
                 <ul >   <li v-for="(lk,idxx) in item.sub" v-bind:key="idxx"><a v-bind:href="lk.url">{{lk.name}}</a></li>  </ul>
        </li>
    </ul>
</div>
</template>

<script>

export default {
  name: 'navbar',
  data () {
    return {
      checked: false,
      links: [
        { title: 'Home', ul: './' },
        {title: 'Products', ul: '#/product', sub: [{name: 'Karaoke Player', url: '#/karaoke'}, {name: 'Amplifier', url: '#/Amplifier'}, {name: 'Microphone', url: '#/Microphone'}]},
        {title: 'Login', ul: '#/login', sub: [{name: 'Register', url: '#/register'}]}
      ],
      title: 'Check me'
    }
  },
  mounted () {
    this.title = 'OK'
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/******************************************************************************/
/*****            Navigation Bar                                         ******/
/******************************************************************************/
#gnav{height:36px; width:100%; background-color:#cc4545; text-align: center; }
#nav { float:right;  margin-right: 216px; line-height: 36px;
        list-style-type: none; background-color:#cc4545;  }
#nav a { display: block; width: 110px; text-align:center; }
#nav a:link     { color:#fff; text-decoration:none; font-weight:bold; font-size: 18px; }
#nav a:visited  { color:#fff; text-decoration:none; font-weight:bold; font-size: 18px; }
#nav a:hover    { color:#FFF; text-decoration:none; font-weight:bold; font-size: 18px; }

#nav li { float: left; width: 120px; background-color:#cc4545; }
#nav li a:hover{ background-color:#cc4545; }

#nav li ul { line-height: 27px; list-style-type: none;text-align:left;left: -999em;
 width: 160px; position: absolute; z-index: 1;}

#nav li ul li{ float: left; width: 156px; background: #e6dcdc; }
#nav li ul a{
display: block; width: 156px;text-align:left;padding-left:12px;
}
#nav li ul a:link {
color:#666; text-decoration:none;
}
#nav li ul a:visited {
color:#666;text-decoration:none;
}
#nav li ul a:hover {
color:#F3F3F3;width: 156px; text-decoration:none;font-weight:normal;
background:#C00;
}
#nav li:hover ul { left: auto; }
#nav li.sfhover ul { left: auto; }
</style>



