<template>
  <div class="pinfo">
    <h6><i class="fas fa-home"></i>  &nbsp; Home > Product</h6>
    <div class="maininf">
      <div class="container-fluid">
        <div class="row">
     <div class="box imgs col-lg-5 col-md-12 align-items-center">
         <img :src="img_url + pitem.image"  width="450" >
    </div><!-- End off imgs  -->
    <div class="box feature col-lg-5 col-md-9">
        <h4>{{pitem.brief}}</h4> <hr>
        <div class="schedule__time" v-if="pitem.descrp">
          <ul>
          <li v-for="(item,inx) in pitem.descrp.feature" :key="inx"> {{item}}</li>
          </ul>
        </div>
    </div><!-- End off feature  -->
    <!-- <div class="box buyoption col-lg-2 col-md-3 ">-->
      <div class="box card col-lg-2 col-md-3 ">
        <div class="card-header">
          <h5>Product Name</h5>
          <span class="tl2">{{pitem.pname}}</span>
          </div>
          <div class="card-body">
       <span class="hd2">Product Model</span><span class="tl2"> &nbsp; &nbsp;{{pitem.model}} </span><hr>
       <button class="btn btn-outline-secondary btn-md btn-block"> Call or visit Store for Price $$ and more detail</button>
       <p class="card-text tl3"> The product warrenty is one year , labour and parts</p>
        <p class="card-text tl3"> Shipping from Vancouver BC Canada </p>
        </div>
       <div class="card-footer">&nbsp; &nbsp;</div>
       </div><!-- End of Card  -->
    <!-- </div>-- End off buyotion  -->
      </div></div>
  </div>  <!-- End of maininf  -->
  <div class="box py-5">
       <div class="container" v-if="pitem.speciff">
         <ul class="nav nav-tab">
           <li class="nav-item"><h5>Specifications</h5></li>
           </ul>
          <table class="table table-striped" >
          <tr v-for="(ls,inx) in pitem.speciff.feature " :key="inx"> <td class = "sname"> {{ls.name}}</td><td class = "sval"> {{ls.sval}}</td></tr>
          </table>
       </div><!-- End of specitem  -->
  </div> <!-- End of box  -->
    <div class="container-fluid ntimg box" v-if="pitem.ntimg" >
       <div class="row">
         <div class="col-lg-8" v-for="(pfl,inx) in pitem.ntimg" :key="inx" >
          <img :src="img_url + pfl" >
          </div>
          </div>
     </div><!-- End of ntimg  -->
  </div><!-- End of pinfo  -->
</template>

<script>

export default {
  name: 'productinfo',
  data () {
    return {
      proId: 'E23008',
      jsd: '',
      pitem: {},
      img_url: 'https://mzhou2015.github.io/gecon_pub/src/vxdagi/image/'
    }
  },
  mounted () {
    if (this.$route.params.pid) {
      this.proId = this.$route.params.pid
    }
    this.getApi(this.proId)
  },
  methods: {
    getApi: function (cval) {
      fetch('https://mzhou2015.github.io/gecon_pub/src/vxdagi/mdata/jsn.json').then((response) => response.text()).then((data) => {
        this.jsd = JSON.parse(data)
        var index = this.jsd.findIndex(x => x.ptcode === cval)
        this.pitem = this.jsd[index]
        console.log(this.pitem)
      }).catch((err) => { console.log(err) })
      return 'OK'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.pinfo {padding:8px; }
.maininf {display: flex; padding-bottom: 8px;}
img {width:100%; padding: 16px; }
.feature{ padding-top:12px ;}
.ntimg{ margin-top: 6px; padding: 10px;}
.hd2 {color: #a32323;font-size: 14px;}
h5 {color: #a32323;}
.tl2 {color: #463343; font-size: 14px;}
.tl3 {color: #463343; font-size: 12px;}

</style>



