<template>
  <div class="listitem">
    <div class="imgx">
     <router-link :to="rtlink"><img :src="img_url+item.image" width="300" ></router-link>
     </div>
     <hr>
     <div class="pname">
      <router-link :to="rtlink">{{item.brief}}</router-link>
      </div>
     <span>Model: {{item.model}}</span>
  </div>
</template>

<script>
// import ...
export default {
  name: 'Prolistitem',
  props: {
    item: Object,
    index: Number
  },
  data () {
    return {
      plink: 'http://www.gecontech.com/magento/myadmin/docs/image/products/',
      purl: 'imagelink',
      img_url: 'https://mzhou2015.github.io/gecon_pub/src/vxdagi/image/',
      rtlink: '/Product/3'
    }
  },
  mounted () {
    this.purl = this.plink + this.item.image
    this.rtlink = '/Product/' + this.item.ptcode
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.listitem {
  width: 370px;
  height: 600px;
  text-align: center;
  margin: 2px;
  padding: 8px;
  background-color:#e1e1eb;
  font-weight: normal;
}
.imgx{
  width: 100%;
  min-height: 360px;
  flex:2 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.pname{
  height: 100px;
  text-align: left;
}
.pname a:hover { text-decoration: none; font-weight: 530;}
span { font-size: 14px; font-weight: 600;}
</style>



