<template>
  <div class="catalog">
    <h6><i class="fas fa-home"></i>  &nbsp; Home > Product</h6>
    <div class="main" >
      <Prolistitem v-for="(items,idx) in jspp" :key="idx" :item="items" :index="idx" />
    </div>
  </div>
</template>

<script>
import Prolistitem from './prolistitem.vue'

export default {
  name: 'Catalog',
  components: {
    Prolistitem
  },
  data () {
    return {
      img_url: 'https://mzhou2015.github.io/gecon_pub/src/vxdagi/image/',
      jspp: {}
    }
  },
  mounted () {
    var cval = 'H23152'
    this.getApi(cval)
  },
  methods: {
    getApi: function (cval) {
      fetch('https://mzhou2015.github.io/gecon_pub/src/vxdagi/mdata/jsn.json').then((response) => response.text()).then((data) => {
        this.jspp = JSON.parse(data)
        var index = this.jspp.findIndex(x => x.ptcode === cval)
        this.psk = this.jspp[index]
        console.log(this.jspp)
      }).catch((err) => { console.log(err) })
      return 'OK'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.catalog {padding: 5px; }
.main { display: flex;
        flex-wrap: wrap;
        justify-content:space-around;
}
</style>



