import Vue from 'vue'
import Router from 'vue-router'
import catalog from '@/components/catalog'
import HelloWorld from '@/components/HelloWorld'
import mytest from '@/components/mytesting'
import productinfo from '@/components/pro_detail/product-info'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'catalog',
      component: catalog
    },
    {
      path: '/Product',
      name: 'ProductInfo',
      component: productinfo
    },
    { path: '/Product/:pid',
      component: productinfo
    },
    {
      path: '/mytest',
      name: 'mytest',
      component: mytest
    },
    {
      path: '/hello',
      name: 'HelloWorld',
      component: HelloWorld
    }
  ]
})



